import React, { useState, useEffect } from 'react';
import {
  Typography,
  Card,
  CardActionArea,
  CardActions,
  CardContent,
  CardMedia,
  Button,
  Grid,
} from '@material-ui/core';

import "./Teaching.scss";
import TeachingPlaceholder from './TeachingPlaceholder';
import imagePlaceholder from '../assets/images/course.jpg';

const cardLists = (data, imgPath) => (
  <Grid container spacing={2} className="data">
    {
      (data || []).map((item) => (
        <Grid item xs={12} lg={6} md={6} className="data-item" key={item.title}>
          <Card>
            <CardActionArea>
              <CardMedia
                className="card-media"
                image={item.img ? `${imgPath}${item.img}` : imagePlaceholder}
                title={item.title}
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="h2">
                  {item.title}
                </Typography>
                <Typography variant="body2" color="textSecondary" component="p">
                  {item.description}
                </Typography>
              </CardContent>
            </CardActionArea>
            <CardActions>
              <Button size="small" color="primary">
                {
                  item.link &&
                  <a href={item.link}>Learn More</a>
                }
                {
                  !item.link &&
                  <>Learn More</>
                }
              </Button>
            </CardActions>
          </Card>
        </Grid>
      ))
    }
  </Grid>
);


export default function Teaching(props) {
  const { loader, imgPath } = props;
  const [data, setData] = useState(null);
  useEffect(() => {
    const fetchData = async () => {
      const res = await fetch("./assets/data/teaching.json");
      const data = await res.json();
      setData(data);
    };
    loader(true);
    fetchData();
    loader(false);
  }, [loader]);
  if (!data) {
    return (
      <TeachingPlaceholder />
    );
  }
  return (
    <div id="Teaching">
      {cardLists(data, imgPath)}
    </div>
  );
}
