import React, { useState, useEffect } from 'react';
import {
  Paper,
  Typography,
  List,
  ListItem,
  ListItemText,
  Link,
} from '@material-ui/core';

import "./Background.scss";
import BackgroundPlaceholder from './BackgroundPlaceholder';

const paperLists = (data) => {
  return Object.keys(data || {}).map((paper) => {
    const { title, icon, items } = data[paper];
    return (
      <Paper key={title} className="data">
        <Typography color="primary" variant="h5" component="h3">
          <i className={icon} />&nbsp; {title}
        </Typography>
        {
          <List className="data-item">
            {
              (items || {}).map((item) => {
                return (
                  <ListItem key={item}>
                    {
                      item[1] &&
                      <Link href={item[1]}>
                        <ListItemText primary={item[0]} />
                      </Link>
                    }
                    {
                      !item[1] &&
                      <ListItemText primary={item[0]} />
                    }
                  </ListItem>
                );
              })
            }
          </List>
        }
      </Paper>
    );
  });
}

export default function Background(props) {
  const { loader } = props;
  const [data, setData] = useState(null);
  useEffect(() => {
    const fetchData = async () => {
      const res = await fetch("./assets/data/background.json");
      const data = await res.json();
      setData(data);
    };
    loader(true);
    fetchData();
    loader(false);
  }, [loader]);
  if (!data) {
    return (
      <BackgroundPlaceholder />
    );
  }
  return (
    <div id="Background">
      {paperLists(data)}
    </div>
  );
}
