import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';
import { Skeleton } from '@material-ui/lab';
import Drawer from '@material-ui/core/Drawer';
import Divider from '@material-ui/core/Divider';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import HomeIcon from '@material-ui/icons/Home';
import BackgroundIcon from '@material-ui/icons/FlipToBack';
import PublicationsIcon from '@material-ui/icons/ChromeReaderMode';
import TeachingIcon from '@material-ui/icons/CastForEducation';
import Typography from '@material-ui/core/Typography';

import Logo from '../assets/images/logo.png';
import "./Header.scss";

const pages = [
  {
    title: "Home",
    icon: <HomeIcon />,
    url: "/home"
  },
  {
    title: "Background",
    icon: <BackgroundIcon />,
    url: "/background"
  },
  {
    title: "Publications",
    icon: <PublicationsIcon />,
    url: "/publications"
  },
  {
    title: "Teaching",
    icon: <TeachingIcon />,
    url: "/teaching"
  },
]

const drawer = ({ name, avatar }, imgPath, { active, setActive }) => {
  return (
    <div className="drawer-content">
      <div className="avatar">
        {
          !avatar &&
          <Skeleton variant="circle" width={175} height={175} />
        }
        {
          avatar &&
          <img src={`${imgPath}${avatar}`} alt={name} />
        }
      </div>
      <div className="name">
        {
          !name &&
          <Skeleton variant="text" width="70%" height={70} style={{margin: "auto"}} />
        }
        {
          name &&
          <Typography align="center" variant="h4" component="h1">{name}</Typography>
        }
      </div>
      <Divider />
      <Divider />
      <Divider />
      <Divider />
      <List>
        {
          pages.map((page, index) => (
            <NavLink onClick={() => setActive(index)} key={page.title} style={{ textDecoration: "none", color: "inherit" }} to={page.url}>
              <ListItem button className={index === active ? "activated" : ""}>
                {/* <ListItemIcon>{page.icon}</ListItemIcon> */}
                <ListItemIcon><i className="far fa-square" /></ListItemIcon>
                <ListItemText primary={page.title} />
              </ListItem>
            </NavLink>
          ))
        }
      </List>
      <Divider />
      <Divider />
      <Divider />
      <Divider />
      <div className="logo"><img src={Logo} alt={`${name}-logo`} /></div>
      <Divider />
      <Divider />
      <Divider />
      <Divider />
      <div className="developer">
        <Typography align="center" variant="caption">Developed By: <u><a href="https://alireza-kavian.github.io">Alireza Kavian</a></u></Typography>
      </div>
    </div >
  );
};

export default function Header(props) {
  let activeInitVal = 0;
  pages.forEach((page, idx) => activeInitVal = (window.location.href.includes(page.url.replace("/", "")) ? idx : activeInitVal));

  const [active, setActive] = useState(activeInitVal);
  const { name, avatar, imgPath } = props;
  const small = window.innerWidth <= 960 ? true : false;
  const anchor = small ? "top" : "left";
  const variant = small ? "persistent" : "permanent";
  return (
    <Drawer
      className="drawer"
      variant={variant}
      anchor={anchor}
      open
      ModalProps={{
        keepMounted: true, // Better open performance on mobile.
      }}
    >
      {drawer({ name, avatar }, imgPath, { active, setActive })}
    </Drawer>
  )
};
