import React, { useState, useEffect } from 'react';
import {
  Paper,
  List,
  ListItem,
  Typography,
  Grid,
  Avatar,
  Link,
} from '@material-ui/core';

import "./Publications.scss";
import PublicationsPlaceholder from './PublicationsPlaceholder';

const paperLists = (data, name) => (
  <div>
    {
      <List className="data">
        {
          (data || []).map((item, idx) => {
            let text = item[0];
            text = String(text).replace(new RegExp(name, "ig"), `<b>${name}</b>`)
            return (
              <ListItem key={item[0]}>
                <Paper elevation={0} className="data-item">
                  <Grid justify="center" alignItems="center" container spacing={0}>
                    <Grid item xs={12} sm={12} lg={1} md={1}>
                      <Typography variant="subtitle2">
                        {idx + 1}
                      </Typography>
                    </Grid>
                    <Grid item xs={12} sm={12} lg={10} md={10}>
                      <Typography variant="subtitle1" dangerouslySetInnerHTML={{ __html: text }}>
                      </Typography>
                    </Grid>
                    <Grid item xs={12} sm={12} lg={1} md={1} className="link-icon">
                      {
                        item[1] &&
                        (
                          <Link href={item[1]} target="_blank">
                            <Avatar variant="circle">
                              <i className="fa fa-external-link-alt" />
                            </Avatar>
                          </Link>
                        )
                      }
                    </Grid>
                  </Grid>
                </Paper>
              </ListItem>
            );
          })
        }
      </List>
    }
  </div>
);


export default function Publications(props) {
  const { loader, name } = props;
  const [data, setData] = useState(null);
  useEffect(() => {
    const fetchData = async () => {
      const res = await fetch("./assets/data/publications.json");
      const data = await res.json();
      setData(data);
    };
    loader(true);
    fetchData();
    loader(false);
  }, [loader]);
  if (!data) {
    return (
      <PublicationsPlaceholder />
    );
  }
  return (
    <div id="Publications">
      {paperLists(data, name)}
    </div>
  );
}
